<script>
import firebase from "firebase";
import { db } from "@/main";
var moment = require("moment");
moment.locale("es");


export default {
  data() {
    return {
      moment: moment,
      users: [],
      userSelect: [],
      updateUserData:{
        name:null,
        email:null,
        uid:null,
        status:null,
      },
      imageData:null,
      picture:null,
      uploadValue: 0,
      newUser:[],
      userID:'',
    };
  },
  filters: {
    formatDate(date) {
      moment.locale("es");

      return moment(date).format("LL");
    },
  },
  mounted() {
    this.getUsers();
  },
  methods: {
    async getUsers() {
      this.users = [];
      try {
        let response = await db.collection("users").where('role', '==', 'Usuario').get().then((doc) => {
            doc.forEach((res) => {
              this.users.push(res.data());
            });
          });

      } catch (error) {
        console.log(error);
      }
    },
    getUserToEdit(id){
      this.userSelect= [];
      db.collection('users').where('uid', '==', id).get().then((doc)=> {
            doc.forEach((res) => {
              
              this.userSelect.push(res.data());
              //console.log(this.userSelect[0].name);
              this.updateUserData.name=this.userSelect[0].name;
              this.updateUserData.email=this.userSelect[0].email;
              this.updateUserData.uid=id;
              this.updateUserData.fotoINE=this.userSelect[0].fotoINE;
              $('#modalEditar').modal('show')
            });
          });
    },
     previewImage(event) {
      this.uploadValue=0;
      this.picture=null;
      this.imageData = event.target.files[0];
    },
    onUpload(){
      this.picture=null;
      const storageRef=firebase.storage().ref('this.imageData.name').put(this.imageData);
      storageRef.on('state_changed',snapshot=>{
        this.uploadValue = (snapshot.bytesTransferred/snapshot.totalBytes)*100;
      }, error=>{console.log(error.message)},
      ()=>{this.uploadValue=100;
        storageRef.snapshot.ref.getDownloadURL().then((url)=>{
          this.picture =url;
        });
      }
      );
    },
     addUser(){

      this.picture=null;
      const storageRef= firebase.storage().ref('this.imageData.name').put(this.imageData);
      storageRef.on('state_changed',snapshot=>{
        this.uploadValue = (snapshot.bytesTransferred/snapshot.totalBytes)*100;
      }, error=>{console.log(error.message)},
      ()=>{this.uploadValue=100;
        storageRef.snapshot.ref.getDownloadURL().then((url)=>{
          this.picture = url;
          //Este alert arroja el url de la imagen

         // alert(this.picture);

          try {
                let response =  firebase.auth().createUserWithEmailAndPassword(this.newUser.email, this.newUser.password)
                if(response){
                    
                    let user = {
                            uid: response.uid,
                            name: this.newUser.name,
                            email: this.newUser.email,
                        }
                   



                    
          
                       db.collection("users").add({
        name:this.newUser.name,
        email:this.newUser.email,
        status:'Activo',
        completeProfile:false,
        firstName:this.newUser.firstName,
        lastName:this.newUser.lastName,
        birthdate:this.newUser.birthdate,
        INE:this.newUser.INE,
        fotoINE:this.picture,
        firstTime:true,
        reason:this.newUser.reason,
        origin:this.newUser.origin,
        telephone:this.newUser.telephone,
        registerDate:new Date(),
        uid:Math.random().toString(36).substring(7),
        role:'Usuario',
        terms:false,
      }).then( ()=> this.getUsers(), this.$swal('Registro Exitoso!'), $('#modalReservacion').modal('hide'))
                    
                    
                    
                }
            } catch(e) {
                switch(e.code){
                  case 'auth/email-already-in-use':
                    alert('Correo electronico ya registrado');
                    break;
                }
                console.log(e);
            }
  
     
          
        });
      }
      );

        



/*
      let response = db.collection("users").add({
        name:this.newUser.name,
        email:this.newUser.email,
        completeProfile:false,
        role:'Usuario',
        terms:false,
      }).then( ()=> this.getUsers(), this.$swal('Registro Exitoso!'), $('#modalReservacion').modal('hide')) */
    },
    updateUser(id){
      db.collection('users').doc(id).update({
        name:this.updateUserData.name,
        email:this.updateUserData.email,
        status:this.updateUserData.status,
      }).then( ()=> this.getUsers(), this.$swal('Usuario Actualizado Con Exito!'), $('#modalEditar').modal('hide'))

    },
    deleteUser(id){

      this.$swal({
  title: '¿Realmente quieres eliminar este usuario?',
  showDenyButton: true,
  showCancelButton: true,
  confirmButtonText: `Eliminar`,
  denyButtonText: `Cancelar`,
}).then((result) => {
  /* Read more about isConfirmed, isDenied below */
  if (result.isConfirmed) {
     db.collection('users').doc(id).delete().then(()=> this.getUsers(), this.$swal('Eliminado!', '', 'success'));
    
  } else if (result.isDenied) {
    this.$swal('No se elimino el usuario', '', 'info')
  }
})
     
    }
  },
};
</script>